@import url('https://fonts.googleapis.com/css2?family=Alkalami&family=Inconsolata:wght@200&family=Poppins:ital,wght@0,300;0,400;0,500;0,600;1,300&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

a{
    text-decoration: none;
}

h1{
    font-family: "Poppins", sans-serif;
    transition: 0.5s;
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 5px;
    padding-left: 15px;
    padding-bottom: 15px;
}

@media screen and (max-width: 1400px){
    h1{
        font-family: "Poppins", sans-serif;
        transition: 0.5s;
        font-size: 12px;
        font-weight: 400;
        letter-spacing: 1px;
        padding-left: 10px;
    }
}

img{
    height: 7%;
    width: 7%;
}

h1:hover{
    color: #ff014f
}

.avatar{
    display: flex;
    justify-content: flex-start;
}